html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Staatliches", cursive;
  font-size: 16px;
  line-height: 1.5;
  background: #f4fde7;
}

p {
  font-size: 2rem;
  font-family: Arial, Helvetica, sans-serif;
}

a {
  text-decoration: none;
  cursor: pointer;
}

img {
  width: 100%;
}

h1, h2, h3 {
  text-align: center;
  font-family: "Staatliches", cursive;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

button,
input[type=button] {
  cursor: pointer;
}

ul {
  padding: 0;
}
ul li {
  list-style-type: none;
}

.container {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
  position: relative;
}

/*--------------------
Buttons
--------------------*/
.btn {
  display: block;
  background: #11e211;
  color: white;
  text-decoration: none;
  margin: 20px 0;
  padding: 15px 15px;
  border-radius: 5px;
  position: relative;
}
.btn::after {
  content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: all 0.2s ease-in-out;
  box-shadow: inset 0 3px 0 rgba(0, 0, 0, 0), 0 3px 3px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
}
.btn:hover::after {
  background: rgba(0, 0, 0, 0.1);
  box-shadow: inset 0 3px 0 rgba(0, 0, 0, 0.2);
}

.btn-social {
  padding-left: 64px;
  position: relative;
  z-index: 1;
}
.btn-social .fa {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 50px;
  height: 100%;
  text-align: center;
  background: rgba(0, 0, 0, 0.1);
  line-height: 3.2;
  border-radius: 5px 0 0 5px;
}

.btn-facebook {
  background-color: #3b5897;
}

.btn-twitter {
  background-color: #53abee;
}

.btn-google {
  background-color: #de4e3b;
}

.btn-linkedin {
  background-color: #00a1db;
}

/*--------------------
Forms
--------------------*/
.form fieldset {
  border: none;
  padding: 0;
  margin: 20px 0;
  position: relative;
}
.form fieldset input {
  width: 100%;
  height: 48px;
  color: #333333;
  padding: 15px 40px 15px 15px;
  border-radius: 5px;
  font-size: 14px;
  outline: none !important;
  border: 1px solid rgba(0, 0, 0, 0.3);
  box-shadow: inset 0 1px 4px rgba(0, 0, 0, 0.2);
  vertical-align: top;
}
.form button {
  width: 100%;
  outline: none !important;
  border: none;
  text-transform: uppercase;
  font-weight: bold;
  box-shadow: 0 3px 0px rgba(115, 136, 89, 0.2);
  text-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);
}

.btn-blue {
  background: blue;
}

.btn-red {
  background: #b11b23;
}

/*--------------------
BigCards
--------------------*/
.cardContainer {
  width: 100%;
  height: auto;
  padding: 5rem 7rem;
}

.card {
  margin: 0 auto;
  width: 800px;
  background: white;
  border-radius: 10px;
  box-shadow: 0 3px 25px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  display: flex;
}

.margin-zero {
  margin: 0;
}

.header {
  background-color: #5EE0FF;
}
.header .header__contents {
  height: 11rem;
  padding: 1rem 0;
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  grid-template-rows: 3fr 1fr;
  grid-template-areas: "logo searchBar buttons" "logo prinNav secNav";
  column-gap: 2.5rem;
}
.header h1 {
  color: #fff;
  text-align: center;
}
.header .nav {
  display: flex;
  gap: 2.5rem;
  justify-content: center;
  align-items: center;
}
.header .nav a {
  color: #fff;
}
.header .nav a:hover {
  color: #b11b23;
}
.header .nav .link--special {
  color: #b11b23;
}
.header .form input,
.header .form button {
  height: 3.5rem;
  font-family: "Staatliches", cursive;
  border: 0;
  color: 900;
  font-size: 1.7rem;
}
.header .form input[type=search] {
  outline: 0;
  width: 100%;
  color: #333333;
  background: #fff;
  border-radius: 0.5rem;
  border: 1px solid rgba(0, 0, 0, 0.3);
  font-size: 14px;
  appearance: none;
  transition: all 0.3s cubic-bezier(0, 0, 0.43, 1.49);
  transition-property: width, border-radius;
  z-index: 1;
  position: relative;
}
.header .form button {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  width: 8rem;
  font-weight: 700;
  background: #11e211;
  border-radius: 0 0.5rem 0.5rem 0;
}
.header .form input:not(:placeholder-shown) {
  border-radius: 0.5rem 0 0 0.5rem;
  width: calc(100% - 8rem);
}
.header .form input:not(:placeholder-shown) + button {
  display: block;
}
.header .form label {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}
.header.fijo {
  position: fixed;
  top: 0;
  width: 100%;
  box-shadow: 0 0.3rem 0.4rem;
  z-index: 3;
}

.logo {
  grid-area: logo;
  margin: auto 0;
}

.nav--principal {
  grid-area: prinNav;
}

.sIsC {
  grid-area: buttons;
  height: 7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
.sIsC .icon svg {
  height: 4rem;
}
.sIsC p {
  margin: 0;
  font-size: 1.2rem;
}
.sIsC .signIn a {
  display: flex;
  justify-content: center;
  align-items: center;
}
.sIsC .shopCart {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.searchBar {
  grid-area: searchBar;
  position: relative;
  margin: auto 0;
}

.nav--secondary {
  grid-area: secNav;
}

.footer {
  background-color: #5EE0FF;
  padding: 1rem;
}
.footer .footer__contents {
  height: 35rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 40% 30% auto;
  grid-template-areas: "links links pay" "ship ship contact" "bot bot bot";
  column-gap: 5rem;
  row-gap: 3rem;
}
.footer .footer__contents p {
  margin: 0;
  font-family: "Staatliches", cursive;
  font-size: 2rem;
}
.footer .footer__contents svg {
  height: 4rem;
}
.footer .links {
  grid-area: links;
  display: flex;
  justify-content: center;
  gap: 2.5rem;
}
.footer .sM {
  grid-area: ship;
  display: flex;
  gap: 5rem;
  align-items: center;
}
.footer .sM .smTitle {
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer .sM .shipMethods {
  display: flex;
  gap: 5rem;
}
.footer .pM {
  grid-area: pay;
  display: flex;
  flex-direction: column;
}
.footer .pM .pmTitle {
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer .pM .payMethods {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
}
.footer .contact {
  grid-area: contact;
}
.footer .bottom {
  grid-area: bot;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.product {
  width: 90%;
  background-color: #5EE0FF;
  margin: 10px auto;
  transition: box-shadow 0.3s;
  align-content: center;
  place-items: center;
  justify-items: center;
  align-items: center;
  margin: 20px;
  border-radius: 0.8rem;
}

.product:hover {
  box-shadow: 0px 0px 10px #646464;
}

.productImage {
  padding: 1.5rem 1rem;
  width: 90%;
  height: auto;
  margin: auto;
}

.productInfo {
  border-top: 1px solid #ebebeb;
  height: auto;
  padding: 15px;
  position: relative;
  z-index: 1;
  font-weight: 700;
}

@media (min-width: 480px) {
  .product {
    box-shadow: 0px 0px 10px #646464;
  }

  .products {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .products article {
    width: 45%;
  }
}
@media (min-width: 720px) {
  .products article {
    width: 20%;
  }
}
.video {
  position: relative;
  max-height: 40rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+62,ffffff+100&0.8+0,0.5+100 */
  background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.61) 62%, rgba(255, 255, 255, 0.5) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.61) 62%, rgba(255, 255, 255, 0.5) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.61) 62%, rgba(255, 255, 255, 0.5) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#cc891c1e", endColorstr="#80ffffff",GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
}
.video img {
  display: block;
  width: 100%;
}

.contenido-video {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
  z-index: 1;
}
.contenido-video h2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: #fff;
  font-size: 7rem;
}
.contenido-video h2 span {
  background-color: #11e211;
  font-size: 2rem;
  padding: 1rem 2rem;
}

.prodBox {
  display: inline-block;
  width: 240px;
  text-align: center;
}
.prodBox .img-container {
  height: 230px;
  width: 200px;
  overflow: hidden;
  border-radius: 0px 0px 20px 20px;
  display: inline-block;
}
.prodBox .img-container img {
  transform: skew(0deg, -13deg);
  height: 250px;
  margin: -35px 0px 0px -70px;
  margin-left: auto;
}
.prodBox .inner-skew {
  display: inline-block;
  border-radius: 20px;
  overflow: hidden;
  padding: 0px;
  transform: skew(0deg, 13deg);
  font-size: 0px;
  margin: 30px 0px 0px 0px;
  background: #c8c2c2;
  height: 250px;
  width: 200px;
  border: 0.1rem solid #b11b23;
}
.prodBox .text-container {
  transition: box-shadow 0.3s;
  padding: 120px 20px 20px 20px;
  border-radius: 0.8rem;
  background: #5EE0FF;
  margin: -120px 0px 0px 0px;
  line-height: 19px;
  font-size: 14px;
}
.prodBox .text-container:hover {
  box-shadow: 0px 0px 10px #646464;
}
.prodBox .text-container h3 {
  margin: 20px 0px 10px 0px;
  color: #b11b23;
  font-size: 18px;
}

.sect-p {
  padding: 1rem 2rem 5rem 2rem;
}

.sect-content {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  row-gap: 5rem;
  align-items: center;
}

@media (min-width: 480px) {
  .text-container {
    box-shadow: 0px 0px 10px #646464;
  }
}
.errorsmain {
  height: 95vh;
  background: #000000;
  text-align: center;
  color: #E0E0E0;
  font-family: "Fira Mono", monospace;
}
.errorsmain h1 {
  font-size: 2.5rem;
  font-family: "Permanent Marker", cursive;
}
.errorsmain div {
  transform-style: preserve-3d;
}
.errorsmain svg {
  width: clamp(300px , 70% , 600px);
  height: 500px;
}
.errorsmain #rocket {
  transform: translateY(750px);
  animation: launch 2s ease-out forwards;
}
@keyframes launch {
  from {
    transform: translateY(750px);
  }
  to {
    perspective: 500px;
    transform: translateY(0px);
  }
}
.errorsmain #stars {
  animation: twinkling 2s linear;
}
@keyframes twinkling {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}
.errorsmain .text {
  opacity: 0;
  animation: appear 1s ease-in forwards;
  animation-delay: 1.8s;
}
@keyframes appear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.errorsmain a {
  color: #F66947;
  text-decoration: none;
}

.pagcontainer {
  padding: 0.5rem;
  padding-bottom: 5rem;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #f4fde7;
  color: #5EE0FF;
}
.pagcontainer ul {
  list-style-type: none;
}
.pagcontainer .items-list {
  max-width: 90vw;
  margin: 2rem;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 3rem;
  justify-content: center;
  align-content: center;
}
@media only screen and (max-width: 600px) {
  .pagcontainer .items-list {
    grid-template-columns: repeat(2, 1fr);
  }
}
.pagcontainer .item {
  width: 10rem;
  height: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #5EE0FF;
  cursor: pointer;
}
.pagcontainer .item span {
  background: #fff;
  box-shadow: 0 0.8rem 2rem rgba(90, 97, 129, 0.05);
  border-radius: 0.6rem;
  padding: 2rem;
  font-size: 3rem;
  transition: all 0.3s ease;
}
.pagcontainer .item:hover span {
  transform: scale(1.2);
  color: #b11b23;
}
.pagcontainer .item p {
  font-size: 1.2rem;
  margin-top: 1rem;
  color: #f4fde7;
}
.pagcontainer .page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 5rem;
  margin: 1rem auto;
  border-radius: 0.6rem;
  background: #fff;
  box-shadow: 0 0.8rem 2rem rgba(90, 97, 129, 0.05);
}
.pagcontainer .page__numbers, .pagcontainer .page__btn, .pagcontainer .page__dots {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.8rem;
  font-size: 1.4rem;
  cursor: pointer;
}
.pagcontainer .page__dots {
  width: 2.6rem;
  height: 2.6rem;
  color: #11e211;
  cursor: initial;
}
.pagcontainer .page__numbers {
  width: 2.6rem;
  height: 2.6rem;
  border-radius: 0.4rem;
}
.pagcontainer .page__numbers:hover {
  color: #b11b23;
}
.pagcontainer .page__numbers.active {
  color: #fff;
  background: #b11b23;
  font-weight: 600;
  border: 1px solid #b11b23;
}
.pagcontainer .page__btn {
  color: #11e211;
}
.pagcontainer .page__btn.active {
  color: #5EE0FF;
  pointer-events: initial;
}
.pagcontainer .page__btn.active:hover {
  color: #b11b23;
}

.detailContainer {
  width: 100%;
  height: auto;
  padding: 5rem 2rem;
}

.detail {
  width: 100%;
  background: white;
  border-radius: 10px;
  box-shadow: 0 3px 25px rgba(0, 0, 0, 0.2);
}

.card-wrapper {
  max-width: 1100px;
  margin: 0 auto;
}
.card-wrapper .img-display {
  overflow: hidden;
}
.card-wrapper .img-showcase {
  display: flex;
  width: 100%;
  transition: all 0.5s ease;
}
.card-wrapper .img-showcase img {
  min-width: 100%;
}
.card-wrapper .img-select {
  display: flex;
}
.card-wrapper .img-item {
  margin: 0.3rem;
}
.card-wrapper .img-item:nth-child(1),
.card-wrapper .img-item:nth-child(2),
.card-wrapper .img-item:nth-child(3) {
  margin-right: 0;
}
.card-wrapper .img-item:hover {
  opacity: 0.8;
}
.card-wrapper .product-content {
  padding: 2rem 1rem;
}
.card-wrapper .product-title {
  font-weight: 700;
  position: relative;
  color: 900;
  margin: 1rem 0;
}
.card-wrapper .product-title::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  height: 4px;
  width: 80px;
  background: #12263a;
}
.card-wrapper .product-rating {
  color: #ffc107;
}
.card-wrapper .product-rating span {
  font-weight: 700;
  color: #252525;
}
.card-wrapper .product-price {
  margin: 1rem 0;
  font-weight: 700;
}
.card-wrapper .product-price span {
  font-weight: 700;
}
.card-wrapper .last-price span {
  color: #f64749;
  text-decoration: line-through;
}
.card-wrapper .new-price span {
  color: #256eff;
}
.card-wrapper .product-detail h2 {
  color: 900;
  padding-bottom: 0.6rem;
}
.card-wrapper .product-detail h2 p {
  padding: 0.3rem;
  opacity: 0.8;
}
.card-wrapper .product-detail h2 ul {
  margin: 1rem 0;
}
.card-wrapper .product-detail h2 ul li {
  margin: 0;
  list-style: none;
  background: url(shoes_images/checked.png) left center no-repeat;
  background-size: 18px;
  padding-left: 1.7rem;
  margin: 0.4rem 0;
  font-weight: 700;
  opacity: 0.9;
}
.card-wrapper .product-detail h2 ul li span {
  font-weight: 400;
  color: #b11b23;
}
.card-wrapper .purchase-info {
  margin: 1.5rem 0;
}
.card-wrapper .social-links {
  display: flex;
  align-items: center;
}
.card-wrapper .social-links a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  color: #000;
  border: 1px solid #000;
  margin: 0 0.2rem;
  border-radius: 50%;
  text-decoration: none;
  font-size: 0.8rem;
  transition: all 0.5s ease;
}
.card-wrapper .social-links a:hover {
  background: #000;
  border-color: transparent;
  color: #fff;
}

@media (min-width: 720px) {
  .card-wrapper {
    padding: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .card-wrapper .card-inner {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1.5rem;
  }
  .card-wrapper .product-imgs {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .card-wrapper .product-content {
    padding-top: 0;
  }
}
.newProd-classic {
  width: 100%;
  padding: 30px 50px;
}
.newProd-classic h1 {
  font-size: 30px;
  margin-top: 10px;
  margin-bottom: 40px;
  text-shadow: 0 2px 3px rgba(0, 0, 0, 0.1);
}
.newProd-classic fieldset::after {
  content: "\f007";
  font-family: FontAwesome;
  position: absolute;
  right: 15px;
  top: 17px;
  z-index: 2;
  width: 20px;
  color: rgba(0, 0, 0, 0.2);
  text-align: center;
}
.newProd-classic fieldset.text::after {
  content: "\e1bf";
}
.newProd-classic fieldset.digit::after {
  content: "\f155";
}
.newProd-classic fieldset.file::after {
  content: "\f03e";
}

