.signup-connect {
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#5ee0ff+0,3690f0+37,1eaefc+59,5ee0ff+86 */
  background: #5ee0ff;
  /* Old browsers */
  background: -moz-linear-gradient(45deg, #5ee0ff 0%, #3690f0 37%, #1eaefc 59%, #5ee0ff 86%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(45deg, #5ee0ff 0%, #3690f0 37%, #1eaefc 59%, #5ee0ff 86%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(45deg, #5ee0ff 0%, #3690f0 37%, #1eaefc 59%, #5ee0ff 86%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#5ee0ff", endColorstr="#5ee0ff",GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
  color: white;
}
